// accordion
$(function() {
  $('#learning-method dd').hide();
  let tg = (s)=>{ $(s).toggleClass('active').next().slideToggle(); };
  $('#learning-method dt').click(function() {
  if(!$(this).hasClass('active')) tg('#learning-method dt.active');
  tg(this);
  }).eq(0).click();
  });

 
